      //   .querySelector(".sidebar-mobile-canvas-icon")
      //   .addEventListener("click", (e) => {
      //     document
      //       .querySelector(".sidebar-mobile-canvas-icon")
      //       .classList.add("changed");

      //     document
      //       .querySelector(".overlay-canvas-sidebar")
      //       .classList.add("changed");

      //     document.querySelector(".primary-sidebar").classList.add("changed");
      //   });

      document
